// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

body {
	background: #000000;
	color: #e1d4aa;
	font-size: 12px;
}

.navbar-default {
	background: #222;
	border: none;
}

.navbar-default .navbar-nav>li>a {
	color: #e1d4aa;
}

.navbar-default .navbar-nav>li>a:hover {
	color: #fff;
}

.panel-default {
	border: none;
}

.castle-tooltip {
	display: none;
}

.counter {
	background: rgba(0, 0, 0, 0.5);
	width: 60%;
	height: 25px;
	color: #fff;
	margin: 0px auto;
	margin-top: 10px;
	border-radius: 5px;
}

.castle-tooltip a {
	border: 1px solid #ccc;
}

.castle-building-panel {
	opacity: .8;
	cursor: pointer;
	transition: opacity 0.3s, visibility 0.3s;
}

.castle-building-panel:hover {
	opacity: 1;
}

.castle-tooltip h5 {
	text-align: center;
	padding-top: 0px !important;
	margin-top: 0px !important;
}

.nav .gold {
	text-align: left;
	background: url('../images/gold.png') no-repeat left center;
	padding-left: 30px;
	margin: 15px 15px;
}

.nav .wood {
	text-align: left;
	background: url('../images/wood.png') no-repeat left center;
	padding-left: 30px;
	margin: 15px 15px;
}

.nav .stone {
	text-align: left;
	background: url('../images/stone.png') no-repeat left center;
	padding-left: 30px;
	margin: 15px 15px;
}

.nav .food {
	text-align: left;
	background: url('../images/food.png') no-repeat left center;
	padding-left: 30px;
	margin: 15px 15px;
}

.world-map,
.castle-map {
	width: 100%;
	background: url('../images/map.jpg') no-repeat center center;
	background-size: cover;
	padding: 30px;
	border: 8px solid #333;
}

.world-map td {
	width: 100px;
	height: 100px;
	border: 1px dashed rgba(0,0,0, 0.2);
	text-align: center;
}

.castle-map td {
	width: 185px;
	height: 145px;
	text-align: center;
}

.world-castle {
	width: 80px;
	display: inline-block;
	margin: 0px auto;
}

.requirements .wood {
	display: block;
	width: 100px;
	position: relative;
	padding-left: 30px;
	background: #333;
	height: 20px;
	font-size: 12px;
	margin-left: 10px;
	line-height: 20px;
}

.requirements .wood .needed:before {
	position: absolute;
	left: -10px;
	top: -5px;
	width: 30px;
	height: 30px;
	content: '';
	background: url('../images/wood.png') #333 center center no-repeat;
	border-radius: 50%;
}

.requirements .stone {
	display: block;
	width: 100px;
	position: relative;
	padding-left: 30px;
	background: #333;
	height: 20px;
	font-size: 12px;
	margin-left: 10px;
	line-height: 20px;
	margin-top: 15px;
}

.requirements .stone .needed:before {
	position: absolute;
	left: -10px;
	top: -5px;
	width: 30px;
	height: 30px;
	content: '';
	background: url('../images/stone.png') #333 center center no-repeat;
	border-radius: 50%;
}

.requirements .time {
	display: block;
	width: 100px;
	position: relative;
	padding-left: 30px;
	background: #333;
	height: 20px;
	font-size: 12px;
	margin-left: 10px;
	line-height: 20px;
	margin-top: 15px;
}

.requirements .time .needed:before {
	position: absolute;
	left: -10px;
	top: -5px;
	width: 30px;
	height: 30px;
	content: '';
	background: url('../images/time.png') #333 center center no-repeat;
	border-radius: 50%;
}

.panel-button {
	display: block;
	margin-top: 15px;
	font-size: 13px !important;
	line-height: 18px !important;
	border: 1px solid #c5ac60 !important;
	width: 100% !important;
	height: 20px !important;
	color: #000 !important;
	text-align: center !important;
	background: -webkit-linear-gradient(-90deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134) !important;
	background: -moz-linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134) !important;
	background: linear-gradient(180deg, rgb(253,218,134) 0, rgb(225,157,60) 100%), rgb(253, 218, 134) !important;
	-webkit-background-origin: padding-box !important;
	background-origin: padding-box !important;
	-webkit-background-clip: border-box !important;
	background-clip: border-box !important;
	-webkit-background-size: auto auto !important;
	background-size: auto auto !important;
	-webkit-box-shadow: 0 -1px 1px 0 rgba(255,255,255,0.701961) inset, 0 1px 1px 0 rgb(177,106,2) !important;
	box-shadow: 0 -1px 1px 0 rgba(255,255,255,0.701961) inset, 0 1px 1px 0 rgb(177,106,2) !important;
	text-shadow: 0 1px 1px rgba(0,0,0,0.498039) !important;
	border-bottom: 4px solid rgb(177,106,2);
	-webkit-border-radius: 7px 7px 9px 9px;
	border-radius: 7px 7px 9px 9px;
}